* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.fixed-wrapper {
  background: orange;
  position: fixed;
  height: 100vh;
  width: 100%;
  clip-path: polygon(
                  0 0, 100% 0, 100% 150px, 40% 0, 0 150px,
                  0 100vh,100% 100vh, 100% calc(100vh - 150px), 60% 100vh,0 calc(100vh - 150px)
  );
}